import React from 'react';
import new_collections from '../../assets/new_collections';

const NewCollection = () => {
  return (
    <>
        <div className="container  py-32">
      <h2 className="font-bold uppercase text-3xl text-center my-10 relative">
  new collections
  <span className="absolute left-1/2  -translate-x-1/2 -bottom-2 w-[100px] border-b-2 border-black"></span>
</h2>
        <div className='grid md:grid-cols-2 lg:grid-cols-4 gap-2'>
        {new_collections.map((ele, index) => {
          return (
            <div key={index} className="p-4 space-y-4 cursor-pointer hover:scale-105 duration-200 transition-all">
              <img src={ele.image} alt="popularImg" />
              <p>{ele.name}</p>
              <div className="flex items-center space-x-4">
                <span className="font-bold">{ele.new_price}</span>
                <span className="text-gray-500 line-through">{ele.old_price}</span>
              </div>
            </div>
          );
        })}
        </div>
     
      </div>
    </>
  )
}

export default NewCollection
