import React from 'react'

const Subsicribe = () => {
  return (
    <>
      <div className="container py-10">
        <div className=' bg-gradient-to-t from-white to-purple-200 text-center py-16 px-6 '>
            <h1 className=' text-3xl lg:text-4xl font-semibold capitalize'>get exclusive offers on your email</h1>

            <div className='relative '>
                <p className='py-5 text-xl '>subscribe to our newsletter and stay update</p>
                <input className=' outline-none w-3/4 lg:w-1/2 p-3 rounded-full mb-4 lg:mb-0 ' type="email" placeholder='Your Email Id' />
                <button className='bg-black text-white px-8 py-3 rounded-full lg:absolute lg:right-60'>subscribe</button>
            </div>
        </div>
      </div>
    </>
  )
}

export default Subsicribe
