import React from "react";
import handIcon from "../../assets/hand_icon.png";
import heroImage from "../../assets/hero_image.png";

const Hero = () => {
  return (
    <>
      <div className="bg-gradient-to-b from-purple-200 to-white">
        <div className="container py-10 flex flex-col lg:flex-row items-center border">
          <div className="  space-y-4">
            <div className="lg:w-3/4 text-center lg:text-left">
              <p className="text-xl font-semibold uppercase">
                new arrivals only
              </p>
              <h1 className=" text-3xl lg:text-6xl font-bold">
                new Collections for everyone
                <img className="w-20 m-auto lg:m-0" src={handIcon} alt="handIcon" />
              </h1>

              <button className="bg-red-600 text-gray-50 px-8 py-3 rounded-full mt-4 hover:bg-red-500 transition-all divide-gray-200">
                Latest Collection
              </button>
            </div>
          </div>

          <div className="lg:ml-32">
            <img className=" w-1/2 m-auto lg:m-0 lg:w-3/4" src={heroImage} alt="heroImage" />
          </div>
        </div>
      </div>
    </>
  );
};

export default Hero;
